.toolbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: white;
  height: 60px;
box-shadow: -4px -3px 14px 3px rgba(168,168,168,1);
}
.toolbar__navigation {
  display: flex;
  flex-direction: row;
  height: 100%;
  align-items: center;
  justify-content: flex-end;
  padding: 0 1rem;
}

.toolbar__logo {
  position: absolute;
  left: 5%;

}

.toolbar__logo a {
  color: black;
  text-decoration: none;
  font-size: 1.5rem;
  font-family: 'Rancho', cursive;
  font-weight: bold;
}

.spacer {
  flex: 1 1;
}

.toolbar_navigation-items ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.toolbar_navigation-items li {
  padding: 0 0.5rem;
}

.toolbar_navigation-items a {
  color: white;
  text-decoration: none;
}

.toolbar_navigation-items a:hover,
.toolbar_navigation-items a:active {
  color: #fa923f;
}

.toolbar__toggle-button {
  justify-self: flex-start;
}


@media (max-width: 768px) {
  .toolbar_navigation-items {
    display: none;
  }
}

@media (min-width: 769px) {
  .toolbar__toggle-button {
    display: none;
  }

  .toolbar__logo {
    margin-left: 0;
  }
}

.side-drawer {
  height: 100%;
  background: white;
  box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 60%;
  max-width: 400px;
  z-index: 200;
  -webkit-transform: translateX(100vw);
          transform: translateX(100vw);
  -webkit-transition: -webkit-transform 0.3s ease-out;
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.side-drawer.open {
  -webkit-transform: translateX(60vw);
          transform: translateX(60vw);
}

.divider {
  border-bottom: 1px solid black;
  margin-bottom: 30px;
  padding-bottom: 8px;
}

.list-element {
  width: 70px;
}

.side-drawer ul {
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.side-drawer li {
  margin: 0.5rem 0;
}

.side-drawer a {
  color: #521751;
  text-decoration: none;
  font-size: 1.2rem;
}

.side-drawer a:hover,
.side-drawer a:active {
  color: #fa923f;
}

@media (min-width: 769px) {
    .side-drawer {
        display: none;
    }
}

.backdrop {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0,0,0,0.3);
    z-index: 100;
}
.landing-page {
  padding: 0 35px 0 35px;
}

.signet {
  width: 180px;
}

.signet-container {
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid lightgray
}

.availability {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.availability > div {
  text-align: center;
  max-width: 90%;
}

.switch-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 24px 0 14px 0;
  border-radius: 10px;
}

.switch {
  font-size: 12px;
  width: 50%;
  padding: 3px 0 3px 0;
  background-color: rgb(188, 188, 188);
  border: none;
  font-weight: bold;
  color:white;
}

.left-switch {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.right-switch {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.form-submission {
  display: flex;
  flex-direction: column;
}

.input-container {
  height: 65px;
  margin-top: 10px;
}

.input-container > label {
  display: block;
  font-size: 12px;
}

.input-container > input {
  border: 1px solid lightgray;
  background-color: #EEEEEE;
  width: 100%;
  height: 36px;
  margin-top: 3px;
}

.main-cta {
  height: 50px;
  width: 95%;
  border-radius: 25px;
  border: none;
  align-self: center;
  font-size: 18px;
  font-weight: 900;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}

.grey-light {
  height: 3px;
}

.traffic-light {
  height: 18px;
}
